import React, { useState } from 'react'

import Base from '../core/Base'
import {Link,Redirect} from 'react-router-dom'

import{signin,authenticate,isAuthenticated} from '../auth/helper'

function Signin(){

  const [values,setValue]=useState({
    email:'',
    password:'',
    error:'',
    loading:false,
    didRedirect:false,
    success:false
  })

  const {email,password,error,loading,didRedirect,success}=values

  const {user}=isAuthenticated()

  const handleChange=name=>event=>{
    setValue({...values,error:false,[name]:event.target.value})
  }

  const onSubmit=event=>{
    event.preventDefault()
    setValue({...values,error:false,loading:true})
    signin({email,password})
    .then(data=>{
      if(data.error){
        setValue({...values,error:data.error,loading:false})
      }
      else{
        authenticate(data,()=>{
          setValue({...values,didRedirect:true})
        })
      }
    })
    .catch(console.log('signin request failed'))
  }

  const performRedirect=()=>{
    // TODO: do a redirection here
    if(didRedirect){
      if(user && user.role===1){
        return <Redirect to="/admin/dashboard" />
      }
      else{
        return <Redirect to="/user/dashboard" />
      }
    }
    if(isAuthenticated()){
      return <Redirect to="/" />
    }
  }

    function signInForm(){
        return(
            <div className="row">
        <div className="col-md-6 offset-sm-3 text-left">
          <form>
            <div className="form-group my-2">
              <label className="text-light">Email</label>
              <input className="form-control" onChange={handleChange("email")} type="email" value={email} />
            </div>

            <div className="form-group my-2">
              <label className="text-light">Password</label>
              <input className="form-control" onChange={handleChange("password")} type="password" value={password}/>
            </div>
            <button className="btn btn-success btn-block" onClick={onSubmit}>Submit</button>
          </form>
        </div>
      </div>
        )
    }

    const loadingMessage=()=>{
      return(
        // if loading true then html runs else not
        loading && (
          <div className='alert alert-info'>
            <h2> Loading...</h2>
          </div>
        )
      )
    }

    const errorMessage=()=>{
      return(
        <div className='alert alert-danger' style={{display:error ? '':'none'}}>
        {error}
      </div>
      )
    }
    return(
        <Base title='Sign In' description='A page for user to sign In'>
            {loadingMessage()}
            {errorMessage()}
            {signInForm()}
            {performRedirect()}
        </Base>
    )
}

export default Signin