 import React, {Fragment} from 'react'
import {Link,withRouter} from 'react-router-dom'

import {signout,isAuthenticated} from '../auth/helper'


function currentTab(history,path){
    if(history.location.pathname===path){
        return {color:"#2ecc72"}
    }
    else{
        return {color:"#FFFFFF"}
    }
}

 function Menu({history}){
     return (
         <div>
             <ul class='nav nav-tabs bg-dark'>
                 <li class='nav-item'>
                     <Link style={currentTab(history,'/')} class='nav-link' to='/'>Home</Link>
                 </li>
                 <li class='nav-item'>
                     <Link style={currentTab(history,'/cart')} class='nav-link' to='/cart'>Cart</Link>
                 </li>
                {isAuthenticated() && isAuthenticated().user.role===0 && (
                     <li class='nav-item'>
                     <Link style={currentTab(history,'/user/dashboard')} class='nav-link' to='/user/dashboard'>U. Dashboard</Link>
                 </li>
                )}
                 {isAuthenticated() && isAuthenticated().user.role===1 && (
                     <li class='nav-item'>
                     <Link style={currentTab(history,'/admin/dashboard')} class='nav-link' to='/admin/dashboard'>A. Dashboard</Link>
                 </li>
                 )}
                 {!isAuthenticated() && (
                     <Fragment>
                        <li class='nav-item'>
                            <Link style={currentTab(history,'/signup')} class='nav-link' to='/signup'>Signup</Link>
                        </li>
                        <li class='nav-item'>
                            <Link style={currentTab(history,'/signin')} class='nav-link' to='/signin'>Sign In</Link>
                        </li>
                     </Fragment>
                 )}
                {isAuthenticated() &&(
                    <li class='nav-item'>
                         <span  class='nav-link text-warning'
                         onClick={()=>{
                             signout(()=>{
                                 history.push('/')
                             })
                         }} >Signout</span>
                    </li>
                )}
                 
             </ul>
         </div>
     )
 }

 export default withRouter(Menu)