import React,{useState,useEffect} from 'react'
import { getProducts} from '../admin/helper/adminapicall'
import '../styles.css'
import Base from './Base'
import Card from './Card'
import { loadCart } from './helper/CartHelper'
import Paymentb from './Paymentb'

function Cart(){

    const [products,setProducts]=useState([])

    const [reload,setReload]=useState(false)

    useEffect(()=>{
        setProducts(loadCart())
    },[reload])

    const loadAllProducts=products=>{
        return (
            <div>
                load product section!
                {products.map((product,index)=>{
                    return(
                        <Card
                    key={index}
                    product={product}
                    addtoCart={false}
                    removeFromCart={true}
                    setReload={setReload}
                    reload={reload}
                    />
                    )
                })}

            </div>
            
        )
    }

    // const loadCheckout=()=>{
    //     return (
    //         <div>load product section</div>
    //     )
    // }
    
    return(
        <Base title="Cart Page" description="Ready to checkout">
      <div className="row text-center">
       <div className="col-6">{products!==undefined && products.length>0 ? loadAllProducts(products): <h3>No product in cart</h3> }</div>
       <div className="col-6">{products!==undefined && products.length>0 ? 
       <Paymentb products={products} setReload={setReload} reload={reload} />: <h3></h3> }</div>

       {/* <div className="col-6"><Paymentb products={products} setReload={setReload} reload={reload} /></div> */}

      </div>
    </Base>
           
    )
}

export default Cart