import React,{useState,useEffect, useLayoutEffect} from 'react'
import AdminDashboard from '../user/AdminDashBoard'
import {Link,Redirect} from 'react-router-dom'
import { getCategories,createProduct } from './helper/adminapicall'
import { isAuthenticated } from '../auth/helper/index'


function AddProduct(){

    const {user,token}=isAuthenticated()

    const [values,setValues]=useState({
        name:'',
        description:'',
        price:'',
        stock:'',
        photo:'',
        categories:[],
        category:'',
        loading:false,
        error:'',
        createdProduct:'',
        getaRedirect:false,
        formData:''
    })

    const {name,description,price,stock,categories,category,loading,error,createdProduct,getaRedirect,formData}=values
    
    const preload=()=>{
        getCategories().then(data=>{console.log(data);
            if(data.error){
                setValues({...values,error:data.error})
            }
            else{
                setValues({...values,categories:data,formData:new FormData()})
               
            }
        })
    }

    useEffect(()=>{
        preload();
    },[])
    
    const handleChange=name=>event=>{console.log(event.target.file)
        const value=name==='photo'? event.target.files[0]: event.target.value
        formData.set(name,value)

        setValues({...values,[name]:value})

        console.log(values)
    }
    const onSubmit=(event)=>{
      event.preventDefault()
      setValues({...values,error:'',loading:true})
      createProduct(user._id,token,formData).then(data=>{
        if(data.error){
          setValues({...values,error:data.error})
        }
        else{
          setValues({...values,name:'',description:'',price:'',photo:'',stock:'',loading:false,createdProduct:data.name    
        })
        
        setTimeout(()=>{setValues({...values,getaRedirect:true})},5000)

        }
      })
    }

    const goBack = () => (
        <div className="mt-5">
          <Link className="btn btn-sm btn-success mb-3" to="/admin/dashboard">
            Admin Home
          </Link>
        </div>
      );

      const successMessage=()=>(
        <h4 className='text-success' style={{display:createdProduct ? '':'none'}}>
            {createdProduct} Added Successfully</h4>
            
      )

      const  performRedirect=()=>{
        if(getaRedirect){
         return <Redirect to='/admin/dashboard'/>
        }
      }

    const createProductForm = () => (
        <form >
          <span>Post photo</span>
          <div className="form-group">
            <label className="btn btn-block ">
              <input
                onChange={handleChange("photo")}
                type="file"
                name="photo"
                accept="image"
                placeholder="choose a file"
              />
            </label>
          </div>
          <div className="form-group">
            <input
              onChange={handleChange("name")}
              name="name"
              className="form-control mb-3"
              placeholder="Name"
              value={name}
            />
          </div>
          <div className="form-group">
            <textarea
              onChange={handleChange("description")}
              name="description"
              className="form-control mb-3"
              placeholder="Description"
              value={description}
            />
          </div>
          <div className="form-group">
            <input
              onChange={handleChange("price")}
              type="number"
              className="form-control mb-3"
              placeholder="Price"
              value={price}
            />
          </div>
          <div className="form-group">
            <select
              onChange={handleChange("category")}
              className="form-control mb-3"
              placeholder="Category"
            >
              <option>Select</option>
              {categories &&
                categories.map((cate,index)=>{
                  return(
                    <option key={index} value={cate._id}>{cate.name}</option>
                  )
                })
              }
              
            </select>
          </div>
          <div className="form-group">
            <input
              onChange={handleChange("stock")}
              type="number"
              className="form-control mb-3"
              placeholder="Quantity"
              value={stock}
            />
          </div>
          
          <button type="submit" onClick={onSubmit} className="btn btn-success">
            Create Product
          </button>
        </form>
      );



    return(
    <AdminDashboard
    title="Create a category here"
    description="Add a new category for new tshirts"
    className=" bg-info p-4">
        <div className="row bg-white rounded">
          <div className="col-md-8 offset-md-2">
          {successMessage()}
          {createProductForm()}
          {performRedirect()}
          {goBack()}
          </div>
        </div>
        
    </AdminDashboard>
    )}

export default AddProduct