import React,{useState} from 'react'
import Base from '../core/Base'
import {isAuthenticated} from '../auth/helper'
import {Link} from 'react-router-dom'
import {createCategory} from './helper/adminapicall'
import AdminDashBoard from '../user/AdminDashBoard'

function AddCategory(){
    const [name,setName]=useState('')
    const [error,setError]=useState(false)
    const [success,setSuccess]=useState(false)
    const {user,token}=isAuthenticated()

    const goBack = () => (
        <div className="mt-5">
          <Link className="btn btn-sm btn-success mb-3" to="/admin/dashboard">
            Admin Home
          </Link>
        </div>
      );

    const handleChange=(event)=>{
        setError('')
        setName(event.target.value)

    }

    const onSubmit=(event)=>{
        event.preventDefault()
        setError('')
        setSuccess(false)

        // backend request
        createCategory(user._id,token,{name})
        .then(data=>{
            if(data.error){
                setError(true)
            }
            else{
                setError('')
                setSuccess(true)
                setName('')
            }
        })
    }
       
    const successMessage=()=>{
        return(
            <h4 className='text-success' style={{display:success ? '':'none'}}>
            Category Created Successfully</h4>
        )
        
    }

    const warningMessage=()=>{
        return(
            <h4 className='text-danger' style={{display:error ? '':'none'}}>
            Faild to create category</h4>
        )
    }

    function myCategoryForm(){
        return(
            <form>
            <div className="form-group">
              <p className="lead">Enter the category</p>
              <input
                type="text"
                className="form-control my-3"
                onChange={handleChange}
                value={name}
                autoFocus
                required
                placeholder="For Ex. Summer"
              />
              <button onClick={onSubmit} className="btn btn-outline-info">Create Category</button>
            </div>
          </form>
        )
    }

    return(
        <AdminDashBoard
        title="Create a category here"
        description="Add a new category for new tshirts"
        className=" bg-info p-4"
      >
        <div className="row bg-white rounded">
          <div className="col-md-8 offset-md-2">
            {successMessage()}
            {warningMessage()}
            {myCategoryForm()}
            {goBack()}
          </div>
        </div>
      </AdminDashBoard>
    )
}

export default AddCategory